						<el-submenu index="2-4">
							<span slot="title"></span>
							<el-menu-item index="/videoreview"></el-menu-item>
							<el-menu-item index="/Allcomment"></el-menu-item>
						</el-submenu>
					</el-submenu>
					<el-submenu index="3">
						<template slot="title">
							<svg class="icon1" aria-hidden="true">
								<use xlink:href="#iconyonghu"></use>
							</svg>
							<span slot="title"></span>
						</template>
						<el-submenu index="3-4">
							<span slot="title"></span>
							<el-menu-item index="/yhgl"></el-menu-item>
							<el-menu-item index="/freeze"></el-menu-item>
						</el-submenu>
						<el-menu-item index="/integral"></el-menu-item>
						<el-menu-item index="/gold"></el-menu-item>
					</el-submenu>
					<el-submenu index="4">
						<template slot="title">
							<svg class="icon1" aria-hidden="true">
								<use xlink:href="#icongongneng"></use>
							</svg>
							<span slot="title"></span>
						</template>
						<el-menu-item index="/digitalnews"></el-menu-item>
						<!-- <el-menu-item index="/goldbuy"></el-menu-item> -->
						<el-menu-item index="/propertytransfer"></el-menu-item>
					</el-submenu>
					<!-- <el-menu-item index="/webplate">
						<svg class="icon1" aria-hidden="true">
							<use xlink:href="#iconbankuai"></use>
						</svg>
						<span slot="title"></span>
					</el-menu-item> -->
					<el-submenu index="6">
						<template slot="title">
							<i class="el-icon-tickets"></i>
							<span slot="title"></span>
						</template>
						<el-menu-item index="/userjournal"></el-menu-item>
						<el-menu-item index="/photoalbum"></el-menu-item>
					</el-submenu>
					<el-menu-item index="/mass">
						<svg class="icon1" aria-hidden="true">
							<use xlink:href="#iconqunfa"></use>
						</svg>
						<span slot="title"></span>
					</el-menu-item>
					<!-- <el-menu-item index="/set">
						<i class="el-icon-setting"></i>
						<span slot="title"></span>
					</el-menu-item> -->
				</el-menu>
			</el-aside>
			<el-container>
				<el-main class="el-main">
					<el-tabs type="border-card">
						<el-tab-pane label="">
							<div style="">
								<el-table style="width: 100%" :data="tableData5.filter(data => !search || data.username.toLowerCase().includes(search.toLowerCase()))">
									<el-table-column label="ID" prop="userid" width="150px" sortable></el-table-column>
									<el-table-column label="" prop="username"></el-table-column>
									<el-table-column label="" prop="useremail" width="300px"></el-table-column>
									<el-table-column label="" prop="time" sortable></el-table-column>
									<el-table-column align="right">
										<template slot="header" slot-scope="scope">
											<el-input v-model="search" size="mini" placeholder="" />
										</template>
										<template slot-scope="scope" width="200px">
											<el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
										</template>
									</el-table-column>
								</el-table>
								
								<el-pagination class="fy" @current-change="handleCurrentChange"  :current-page="page" background layout="prev, pager, next" :total="totl" :page-size="5">
								</el-pagination>
							</div>
						</el-tab-pane>
						
						<el-tab-pane label="">
							<div style="">
								<el-table style="width: 100%" :data="tableData.filter(data => !search || data.username.toLowerCase().includes(search.toLowerCase()))">
									<el-table-column label="ID" prop="userid" width="150px" sortable></el-table-column>
									<el-table-column label="" prop="username"></el-table-column>
									<el-table-column label="" prop="useremail" width="300px"></el-table-column>
									<el-table-column label="" prop="time" sortable></el-table-column>
									<el-table-column align="right">
										<template slot="header" slot-scope="scope">
											<el-input v-model="search" size="mini" placeholder="" />
										</template>
										<template slot-scope="scope" width="200px">
											<el-button v-if="scope.row.freeze == 0" size="mini" @click="handleEdit1(scope.$index, scope.row)"></el-button>
										</template>
									</el-table-column>
								</el-table>
								
								<el-pagination class="fy" @current-change="handleCurrentChange1" :current-page="page1" background layout="prev, pager, next" :total="totl1" :page-size="5">
								</el-pagination>
