<script>
	export default {
		inject: ['reload'],
		data() {
			return {
				isCollapse: true,
				adminname: '',
				search: '',
				tableData5: [{
					userid: '',
					username: '',
					useremail: '',
					freeze: '',
					time : ''
				}],
				tableData: [{
					userid: '',
					username: '',
					useremail: '',
					freeze: '',
					time : ''
				}],
				page: 1,
				totl: 0,
				page1 : 1,
				totl1 :0
			};
		},
		methods: {
			handleCurrentChange(val) {
				this.page = val;
				this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/findPageNotFrozen.do', {
					params: {
						page: this.page,
						origin: window.location.origin
					}
				})
				.then(rs => {
					var qwe = Math.ceil(rs.data.count);
					this.totl = qwe;
					this.tableData5 = rs.data.xyuser
				}).catch(err => {
				});
			},
			handleCurrentChange1(val) {
				this.page1 = val;
				this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/findPageFrozen.do', {
					params: {
						page: this.page1,
						origin: window.location.origin
					}
				})
				.then(rs => {
					var qwe = Math.ceil(rs.data.count);
					this.totl1 = qwe;
					this.tableData = rs.data.xyuser
				}).catch(err => {
				});
			},
			handleOpen(key, keyPath) {
			},
			handleClose(key, keyPath) {
			},
			handleEdit(index, row) {
				var self = this;
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
					center: true
				}).then(() => {
					this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/updateByUserIdF.do', {
						params: {
							userid: row.userid
						}
					})
					.then(rs => {
						self.$message({
							message: '',
							type: 'success',
							onClose: function() {
								self.reload();
							}
						})
					}).catch(err => {
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					});
				});
			},
			handleEdit1(index, row) {
				var self = this;
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
					center: true
				}).then(() => {
					this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/updateByUserIdNotF.do', {
						params: {
							userid: row.userid
						}
					})
					.then(rs => {
						self.$message({
							message: '',
							type: 'success',
							onClose: function() {
								self.reload();
							}
						})
					}).catch(err => {
						//console.log(err)
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					});
				});
				//console.log(index, row);
			},
			logout() {
				var self = this;
				self.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: ''
				}).then(() => {
					this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/desSession.do', {
							params: {
								origin: window.location.origin
							}
						})
						.then(rs => {
							//console.log(rs.data.xyuser)
							self.$message({
								message: '',
								type: 'success',
								onClose: function() {
									self.$router.push({path: '/login'});
								}
							})
						}).catch(err => {
							//console.log(err)
						})
				}).catch(() => {
					//console.log(err)
				})
			}
		},
		mounted() {
			var self = this;
			this.$axios.get('http://111.230.247.223/SameBWeb/xyadministrator/findSession.do', {
					params: {
						origin: window.location.origin
					}
				})
				.then(rs => {
					self.adminname = rs.data.xyuser.adminname;
					if(self.adminname === ''){
						self.$router.push({ path: '/login' });
					}
				}).catch(err => {
					//console.log(err)
				});

			this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/findPageNotFrozen.do', {
				params: {
					page: self.page,
					origin: window.location.origin
				}
			})
			.then(rs => {
				var qwe = Math.ceil(rs.data.count);
				self.totl = qwe;
				//console.log(rs.data)
				this.tableData5 = rs.data.xyuser
			}).catch(err => {
				//console.log(err)
			});
			
			
			this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/findPageFrozen.do', {
				params: {
					page: self.page1,
					origin: window.location.origin
				}
			})
			.then(rs => {
				var qwe = Math.ceil(rs.data.count);
				self.totl1 = qwe;
				//console.log(rs.data)
				this.tableData = rs.data.xyuser
			}).catch(err => {
				//console.log(err)
			});
		}
	}
</script>

