							<i class="el-icon-tickets"></i>
							<span slot="title"></span>
						</template>
						<el-menu-item index="/userjournal"></el-menu-item>
						<el-menu-item index="/photoalbum"></el-menu-item>
					</el-submenu>
					<el-menu-item index="/mass">
						<svg class="icon1" aria-hidden="true">
							<use xlink:href="#iconqunfa"></use>
						</svg>
						<span slot="title"></span>
					</el-menu-item>
					<!-- <el-menu-item index="/set">
						<i class="el-icon-setting"></i>
						<span slot="title"></span>
					</el-menu-item> -->
				</el-menu>
			</el-aside>
			<el-container>
				<el-main class="el-main">
					<el-tabs type="border-card">
						<el-tab-pane label="">
							<div style="">
								<el-table style="width: 100%" :data="tables.slice((pageIndex-1)*pageSize,pageIndex*pageSize)">
									<el-table-column label="" prop="title"></el-table-column>
									<el-table-column label="" prop="source"></el-table-column>
									<el-table-column :show-overflow-tooltip="true" label="" prop="newtext"></el-table-column>
									<el-table-column :show-overflow-tooltip="true" label="" prop="time" sortable></el-table-column>
									<el-table-column label="" prop="anthor" ></el-table-column>
									<el-table-column label="" prop="clickcount"></el-table-column>
									<el-table-column align="right">
										<template slot="header" slot-scope="scope">
											<el-input v-model="search" size="mini" placeholder="" />
										</template>
										<template slot-scope="scope" width="200px">
											<el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
										</template>
