<script>
	export default {
		inject: ['reload'],
		data() {
			return {
				pageSize: 5, // 
				pageIndex: 1, // 
				isCollapse: true,
				adminname: '',
				dialogTableVisible: false,
				formLabelWidth: '120px',
				fileList: [],
				form : {
					title: '',
					newtext: '',
					anthor: '',
					source: ''
				},
				rules: {
					title: [{ required: true, message: '', trigger: 'blur' }],
					newtext: [{ required: true, message: '', trigger: 'change' }],
					anthor: [{ required: true, message: '', trigger: 'change' }],
					source : [{required: true, message: '', trigger: 'blur'}]
				},
				search: '',
				tableData5: [{
					adminid : '',
					anthor : '',
					clickcount : '',
					newsid : '',
					newtext : '',
					source : '',
					time: '',
					title: '',
				}],
				page: 1,
				totl: 0,
			};
		},
		methods: {
			handleOpen(key, keyPath) {
			},
			handleClose(key, keyPath) {
			},
			onSubmit(formName) {  //
				this.$refs[formName].validate((valid) => {
					if (valid) {
						var self = this;
						this.$axios.post('http://111.230.247.223/SameBWeb/xynews/insertOne.do',{
								title: self.form.title,
								newtext: self.form.newtext,
								anthor: self.form.anthor,
								source: self.form.source
						}).then( rs => {
							this.$message({
								type: 'success',
								message: '!',
								onClose:function(){
									self.reload();
								}
							});
						});
					} else {
						return false;
					}
				});
			},
			handleDelete(index, row) {
				var self = this;
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
					center: true
				}).then(() => {
					this.$axios.get('http://111.230.247.223/SameBWeb/xynews/deleteById.do', {
						params: {
							newsid: row.newsid,
						}
					})
					.then(rs => {
						this.$message({
							type: 'success',
							message: '!',
							onClose:function(){
								self.reload();
							}
						});
					}).catch(err => {
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					});
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			logout() {
				var self = this;
				self.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: ''
				}).then(() => {
					this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/desSession.do', {
						params: {
							origin: window.location.origin
						}
					})
					.then(rs => {
						self.$message({
							message: '',
							type: 'success',
							onClose: function() {
								self.$router.push({path: '/login'});
							}
						})
					}).catch(err => {
					})
				}).catch(() => {
				})
			},
			format (val) {
				val = val.toString()
				if (val.indexOf(this.search) !== -1 && this.search !== '') {
					return val.replace(this.search, '<font color="red">' + this.search + '</font>')
				} else {
					return val
				}
			},
			handleSizeChange (val) {
			  this.pageSize = val
			},
			handleCurrentChange (val) {
			  this.pageIndex = val
			},
			siteTableHeader () {
			  return 'sws-table-header'
			},
			tableRowClassName ({ row, rowIndex }) {
			  if (rowIndex % 2) {
				return 'warning-row'
			  } else {
				return 'success-row'
			  }
			}
		},
		mounted() {
			var self = this;
			this.$axios.get('http://111.230.247.223/SameBWeb/xyadministrator/findSession.do', {
				params: {
					origin: window.location.origin
				}
			})
			.then(rs => {
				self.adminname = rs.data.xyuser.adminname;
				if(self.adminname === ''){
					self.$router.push({ path: '/login' });
				}
			}).catch(err => {
			});
			
			this.$axios.get('http://111.230.247.223/SameBWeb/xynews/findAll.do')
			.then(rs => {
				this.tableData5 = rs.data.xyuser
			}).catch(err => {
			});
		},
		computed: {
			// 
			tables () {
			  const search = this.search
			  if (search) {
				return this.tableData5.filter(dataNews => {
				  return Object.keys(dataNews).some(key => {
					return String(dataNews[key]).toLowerCase().indexOf(search) > -1
				  })
				})
			  }
			  return this.tableData5
			},
			// 
			total () {
			  return this.tables.length
			}
		},
		watch: {
		   // 
			tables () {
			  this.pageIndex = 1
			}
		},
	}
</script>

