						required: true,
						type: 'email',
						message: '',
						trigger: 'blur'
					}],
					sex: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					phone: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					schooling: [{
						message: '',
