				this.$refs[formName].validate((valid) => {
					if (valid) {
						this.$axios.get('http://111.230.247.223/SameBWeb/xyadministrator/insertByAdmin.do', {
							params:{
								adminname: this.form1.adminname,
								adminpassword: this.form1.adminpassword,
							}
						})
						.then(rs => {
							this.$notify({
								title: '',
								message: '',
								type: 'success',
								onClose: function() {
									self.reload();
								}
							});
						}).catch(err => {
							//console.log(err)
							this.$notify.error({
								title: '',
								message: ''
							});
						})
					} else {
						//console.log('error submit!!');
						return false;
					}
				});
			},
			handleDelete(index, row) {   //
				var self = this;
				if(row.freeze == 1){
					this.$confirm(', ?', '', {
						confirmButtonText: '',
						cancelButtonText: '',
						type: 'warning',
						center: true
					}).then(() => {
						this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/updateByUserIdF.do', {
							params: {
								userid: row.userid
							}
						})
						.then(rs => {
							self.$message({
								message: '',
								type: 'success',
								onClose: function() {
									self.reload();
								}
							})
						}).catch(err => {
							//console.log(err)
						})
					}).catch(() => {
						this.$message({
							type: 'info',
							message: ''
						});
					});
				}else{
					this.$confirm(', ?', '', {
						confirmButtonText: '',
						cancelButtonText: '',
						type: 'warning',
						center: true
					}).then(() => {
						this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/updateByUserIdNotF.do', {
							params: {
								userid: row.userid
							}
						})
						.then(rs => {
							self.$message({
								message: '',
								type: 'success',
								onClose: function() {
									self.reload();
								}
							})
						}).catch(err => {
							//console.log(err)
						})
					}).catch(() => {
						this.$message({
							type: 'info',
							message: ''
						});
					});
				}
			},
			submitForm(formName) {  //
				this.$refs[formName].validate((valid) => {
					if (valid) {
						var self = this;
						this.$axios({
							method: 'post',
							url: 'http://ouyangwende.club/SameBWeb/xyuserinfo/updateByUserId.do',
							data: {
								userid: self.ruleForm.userid,
								userphoto: self.ruleForm.userphoto,
								city: self.ruleForm.city,
								birthday: this.format(self.ruleForm.birthday),
								sex: self.ruleForm.sex,
								phone: self.ruleForm.phone,
								usershow: self.ruleForm.usershow,
								schooling: self.ruleForm.schooling,
								monthincoming: self.ruleForm.monthincoming,
								work: self.ruleForm.work,
								homeincoming: self.ruleForm.homeincoming,
								qq: self.ruleForm.qq,
								msn: self.ruleForm.msn
							},
							withCredentials: true,
						}).then(function(res) {
							self.$message({
								message: '',
								type: 'success',
								onClose: function() {
									self.reload();
								}
							})
						});
					} else {
						return false;
					}
				});
			},
			resetForm(formName) { //
				this.$refs[formName].resetFields();
			},
			resetForm1(formName) {
				this.$refs[formName].resetFields();
			},
			resetForm2(formName) {
				this.$refs[formName].resetFields();
			},
			logout() { //
				var self = this;
				self.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: ''
				}).then(() => {
					this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/desSession.do', {
						params: {
							origin: window.location.origin
						}
					})
					.then(rs => {
						self.$message({
							message: '',
							type: 'success',
							onClose: function() {
								self.$router.push({
									path: '/login'
								});
							}
						})
					}).catch(err => {
						//console.log(err)
					})
				}).catch(() => {
					//console.log(err)
				})
			},
			format(Date) { 				// date  string
				var Y = Date.getFullYear();
				var M = Date.getMonth() +1;
				M = M < 10 ? '0' + M : M; // 0
				var D = Date.getDate();
				D = D < 10 ? '0' + D : D;
				return Y + '-' + M + '-' + D;
			},
			toDate(dateString) { 		//string  date
				var DATE_REGEXP = new RegExp("(\\d{4})-(\\d{2})-(\\d{2})([T\\s](\\d{2}):(\\d{2}):(\\d{2})(\\.(\\d{3}))?)?.*");
				if (DATE_REGEXP.test(dateString)) {
					var timestamp = dateString.replace(DATE_REGEXP, function($all, $year, $month, $day, $part1, $hour, $minute,
						$second, $part2, $milliscond) {
						var date = new Date($year, $month-1, $day, $hour || "00", $minute || "00", $second || "00", $milliscond || "00");
						return date.getTime();
					});
					var date = new Date();
					date.setTime(timestamp);
					return date;
				}
				return null;
			},
			fomat (val) {
				val = val.toString()
				if (val.indexOf(this.search) !== -1 && this.search !== '') {
					return val.replace(this.search, '<font color="red">' + this.search + '</font>')
				} else {
					return val
				}
			},
			handleSizeChange (val) {
			  this.pageSize = val
			},
			handleCurrentChange (val) {
			  this.pageIndex = val
			},
			siteTableHeader () {
			  return 'sws-table-header'
			},
			tableRowClassName ({ row, rowIndex }) {
			  if (rowIndex % 2) {
				return 'warning-row'
			  } else {
				return 'success-row'
			  }
			}
		},
		mounted() {
			var self = this;
			this.$axios.get('http://111.230.247.223/SameBWeb/xyadministrator/findSession.do')
			.then(rs => {
				self.adminid = rs.data.xyuser.adminid;
				self.adminname = rs.data.xyuser.adminname;
				if (self.adminname === '') {
					self.$router.push({ path: '/login' });
				}
			}).catch(err => {
				//console.log(err)
			});

			
			this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/findAll.do')
			.then(rs => {
				this.tableData5 = rs.data.xyuser
				//console.log(rs.data.xyuser)
			}).catch(err => {
				//console.log(err)
			});
		},
		computed: {
			// 
			tables () {
			  const search = this.search
			  if (search) {
				return this.tableData5.filter(dataNews => {
				  return Object.keys(dataNews).some(key => {
					return String(dataNews[key]).toLowerCase().indexOf(search) > -1
				  })
				})
			  }
			  return this.tableData5
			},
			// 
			total () {
			  return this.tables.length
			}
		},
		watch: {
		   // 
			tables () {
			  this.pageIndex = 1
			}
		},
	}
</script>

