				<el-dropdown-menu slot="dropdown">
					<el-dropdown-item><a @click="logout"></a></el-dropdown-item>
				</el-dropdown-menu>
			</el-dropdown>
		</el-header>
		<el-container>
			<el-aside width="65px">
				<el-menu default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
				 :unique-opened="true" :collapse="isCollapse" :router="true">
					<el-menu-item index="/index">
						<i class="el-icon-loading"></i>
						<span slot="title"></span>
					</el-menu-item>
					<el-submenu index="2">
						<template slot="title">
							<svg class="icon1" aria-hidden="true">
								<use xlink:href="#iconshipin1"></use>
							</svg>
							<span slot="title"></span>
						</template>
						<el-menu-item index="/video"></el-menu-item>
						<el-menu-item index="/videoaudit"></el-menu-item>
						<el-menu-item index="/videotype"></el-menu-item>
						<el-submenu index="2-4">
							<span slot="title"></span>
							<el-menu-item index="/videoreview"></el-menu-item>
							<el-menu-item index="/Allcomment"></el-menu-item>
						</el-submenu>
					</el-submenu>
					<el-submenu index="3">
						<template slot="title">
							<svg class="icon1" aria-hidden="true">
								<use xlink:href="#iconyonghu"></use>
							</svg>
							<span slot="title"></span>
						</template>
						<el-submenu index="3-4">
							<span slot="title"></span>
							<el-menu-item index="/yhgl"></el-menu-item>
							<el-menu-item index="/freeze"></el-menu-item>
						</el-submenu>
						<el-menu-item index="/integral"></el-menu-item>
						<el-menu-item index="/gold"></el-menu-item>
					</el-submenu>
					<el-submenu index="4">
						<template slot="title">
							<svg class="icon1" aria-hidden="true">
								<use xlink:href="#icongongneng"></use>
							</svg>
							<span slot="title"></span>
						</template>
						<el-menu-item index="/digitalnews"></el-menu-item>
						<!-- <el-menu-item index="/goldbuy"></el-menu-item> -->
						<el-menu-item index="/propertytransfer"></el-menu-item>
					</el-submenu>
					<!-- <el-menu-item index="/webplate">
						<svg class="icon1" aria-hidden="true">
							<use xlink:href="#iconbankuai"></use>
						</svg>
						<span slot="title"></span>
					</el-menu-item> -->
					<el-submenu index="6">
						<template slot="title">
							<i class="el-icon-tickets"></i>
							<span slot="title"></span>
						</template>
						<el-menu-item index="/userjournal"></el-menu-item>
						<el-menu-item index="/photoalbum"></el-menu-item>
					</el-submenu>
					<el-menu-item index="/mass">
						<svg class="icon1" aria-hidden="true">
							<use xlink:href="#iconqunfa"></use>
						</svg>
						<span slot="title"></span>
					</el-menu-item>
					<!-- <el-menu-item index="/set">
						<i class="el-icon-setting"></i>
						<span slot="title"></span>
					</el-menu-item> -->
				</el-menu>
			</el-aside>
			<el-container>
				<el-main class="el-main">
					<el-tabs type="border-card">
						<el-tab-pane label="">
							<el-table :data="tables.slice((pageIndex-1)*pageSize,pageIndex*pageSize)"  style="width: 100%">
								<el-table-column label="ID" prop="infoid" width="100px" sortable></el-table-column>
								<el-table-column :show-overflow-tooltip="true" label="" prop="title"></el-table-column>
								<el-table-column :show-overflow-tooltip="true" label="" prop="text"></el-table-column>
								<el-table-column :show-overflow-tooltip="true" label="" prop="time"></el-table-column>
								<el-table-column align="right">
									<template slot="header" slot-scope="scope">
										<el-input v-model="search" size="mini" placeholder="" />
									</template>
									<template slot-scope="scope" width="200px">
										<el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
									</template>
								</el-table-column>
							</el-table>
							
							 <el-pagination
								@size-change="handleSizeChange"
								@current-change="handleCurrentChange"
								:current-page="pageIndex"
								:page-sizes="[5, 6, 7, 8]"
								:page-size="pageSize"
								layout="total, sizes, prev, pager, next, jumper"
								:total="total">
							</el-pagination>
						</el-tab-pane>
						<el-tab-pane label="">
							<el-popover  placement="top-start"  width="50%"  trigger="click">
								<el-form ref="form" :model="form" label-width="100px">
									<el-form-item label="" style="width: 500px;">
										<el-input v-model="form.zt"></el-input>
									</el-form-item>
									<el-form-item label="">
										<el-input type="textarea" v-model="form.desc"></el-input>
									</el-form-item>
									<el-form-item>
										<el-button type="primary" @click="onSubmit"></el-button>
										<el-button></el-button>
									</el-form-item>
								</el-form>
							  <el-button slot="reference"></el-button>
							  
							</el-popover>
