</template>

<script>
	export default {
		inject: ['reload'],
		data() {
			return {
				pageSize: 5, // 
				pageIndex: 1, // 
				isCollapse: true,
				adminname: '',
				fileList: [],
				form : {
					zt: '',
					desc: ''
				},
				search: '',
				vip:{
					qt:'',
					by:'',
					hj:''
				},
				tableData:[{
					infoid: '',
					text: '',
					time: '',
					title: '',
				}],
			}
		},
		methods: {
			yonghu() {
			  this.$notify({
			    title: '',
			    message: ''
			  });
			},
			onSubmit() {
			 	var a = this;
				this.$axios.post('http://111.230.247.223/SameBWeb/xysysteminfo/insertData.do', {
					title : a.form.zt ,
					text : a.form.desc
				})
				.then(rs => {
					this.$message({
						type: 'success',
						message: '!',
						onClose:function(){
							a.reload();
						}
					});
				}).catch(err => {
					//console.log(err)
				});
			},
			biaodan(v){
				var b=this;
				//console.log(v);
				b.form.name1=v;
			},
			handleOpen(key, keyPath) {
				//console.log(key, keyPath);
			},
			handleClose(key, keyPath) {
				//console.log(key, keyPath);
			},
			handleDelete(index, row) {
				//console.log(index, row);
				var self = this;
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
					center: true
				}).then(() => {
					this.$axios.get('http://111.230.247.223/SameBWeb/xysysteminfo/deleteById.do', {
						params: {
							infoid: row.infoid,
						}
					})
					.then(rs => {
						this.$message({
							type: 'success',
							message: '!',
							onClose:function(){
								self.reload();
							}
						});
					}).catch(err => {
						//console.log(err)
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					});
				});
			},
			logout() {
				var self = this;
				self.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: ''
				}).then(() => {
					this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/desSession.do', {
							params: {
								origin: window.location.origin
							}
						})
						.then(rs => {
							//console.log(rs.data.xyuser)
							self.$message({
								message: '',
								type: 'success',
								onClose: function() {
									self.$router.push({path: '/login'});
								}
							})
						}).catch(err => {
							//console.log(err)
						})
				}).catch(() => {
					//console.log(err)
				})
			},	
			
			format (val) {
				val = val.toString()
				if (val.indexOf(this.search) !== -1 && this.search !== '') {
					return val.replace(this.search, '<font color="red">' + this.search + '</font>')
				} else {
					return val
				}
			},
			handleSizeChange (val) {
			  this.pageSize = val
			},
			handleCurrentChange (val) {
			  this.pageIndex = val
			},
			siteTableHeader () {
			  return 'sws-table-header'
			},
			tableRowClassName ({ row, rowIndex }) {
			  if (rowIndex % 2) {
				return 'warning-row'
			  } else {
				return 'success-row'
			  }
			}
		},
		mounted() {
			var self = this;
			this.$axios.get('http://111.230.247.223/SameBWeb/xyadministrator/findSession.do', {
				params: {
					origin: window.location.origin
				}
			})
			.then(rs => {
				self.adminname = rs.data.xyuser.adminname;
				if(self.adminname === ''){
					self.$router.push({ path: '/login' });
				}
			}).catch(err => {
				//console.log(err)
			});
			
			
			this.$axios.get('http://111.230.247.223/SameBWeb/xysysteminfo/findAll.do')
			.then(rs => {
				self.tableData = rs.data.xyuser
				//console.log(rs.data.xyuser)
			}).catch(err => {
				//console.log(err)
			});
		},
	
	computed: {
		// 
		tables () {
		  const search = this.search
		  if (search) {
			return this.tableData.filter(dataNews => {
			  return Object.keys(dataNews).some(key => {
				return String(dataNews[key]).toLowerCase().indexOf(search) > -1
			  })
			})
		  }
		  return this.tableData
		},
		// 
		total () {
		  return this.tables.length
		}
	},
	watch: {
	   // 
		tables () {
		  this.pageIndex = 1
		}
	},
  }
</script>

